<template>
  <div>
    <canvas id="sakura" width="400" height="920"></canvas>
    <div id="content" style="z-index: 2;position: relative;"><br>
      <div class="search_part"><br>
        <form id="search_form" @submit.prevent class="search_bar">
          <input type="submit" id="search_submit" value="✢">
          <span><i id="clear" v-show="isClearVisible" @click="clear_seach"></i>
      <div class="si">
        <input class="search" type="text" value="" @keyup="get_suggest" @focus="get_suggest"
               @paste="get_suggest" autocomplete="off" @keyup.enter="search" v-model="searchInput" ref="search_input" id="search_input"
               placeholder="✎...">
      </div>
      </span>
          <div id="suggest" v-show="isSuggestVisible">
            <ul id="suglist">
              <li v-for="(item,index) in urls" :key="index">{{item}}</li>
            </ul>
            <div class="close" onclick="close_sug()">| 收起</div>
          </div>
        </form>
      </div>
      <br>
      <div class="box"><a href="#"></a>
        <p><img class="icon" src="@/assets/img/yhbg/jiaowuxitong.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href="#"></a>
        <p><img class="icon" src="@/assets/img/yhbg/tieba.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href="#"></a>
        <p><img class="icon" src="@/assets/img/yhbg/google_plus5.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href="#"></a>
        <p><img class="icon" src="@/assets/img/yhbg/tupian.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/wandoujia_youxiribao.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/yingyue1.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/echooit.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/meihua.png"></p>
        <p class="url">啥也没有</p>
      </div>
    </div>
    <div id="app-items">
      <div class="title">{{ title }}</div>
      <br>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/xiaoshuo.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/baiduyun.png"></p>
