      </div>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/youxiang.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div class="box"><a href=""></a>
        <p><img class="icon" src="@/assets/img/yhbg/web-app.png"></p>
        <p class="url">啥也没有</p>
      </div>
      <div id="yiyan">
        <div class="shuxing">
          <div id="lwlhitokoto">爱恨两相难，正邪一念间。</div>
        </div>
        <br>
      </div>
    </div>
  </div>
</template>


<script>
import InitYh from "./js/initYh";
export default {
  name: "yhbg",
  data() {
    return {
      title: "",
      searchInput: "",
      isSuggestVisible: false,
      isClearVisible: false,
      last_kw: "",
      max_sug_len: 1,
      urls:[]
    };

  },
  mounted() {
    this.$nextTick(() => {
      this.init();
    });
  },
  methods: {
    init() {
      var d = new Date();
      var time = d.getHours();
      if (time < 24) {
        this.title = "Good evening!";
      }
      if (time < 19) {
        this.title = "Good afternoon!";
      }
      if (time < 12) {
        this.title = "Good morning!";
      }
      if (time < 5) {
        this.title = "Go to sleep!";
      }
      new InitYh();
      window.addEventListener("resize", this.move_input);
      document.getElementById("suglist").addEventListener("click", (e) => {
        if (e.target.tagName == "B") {
          this.searchInput = e.target.parentNode.firstChild.textContent;
          this.$refs.search_input.focus();
        } else if (e.target.tagName == "LI") {
          this.searchInput = e.target.firstChild.textContent;
          this.close_sug();
          this.search();
        }
      });
    },
    clear_seach() {
      this.searchInput = "";
      this.isClearVisible = false;
      this.close_sug();
      this.$refs.search_input.focus();
    },
    close_sug() {
      this.last_kw = "";
      this.isSuggestVisible = false;
    },
    get_suggest() {
      if (this.searchInput) this.isClearVisible = true;
      else this.isClearVisible = false;
      if (this.searchInput == this.last_kw) return;
      this.last_kw = this.searchInput;
      if (!this.searchInput || this.searchInput.length < this.max_sug_len) {
        this.close_sug();
        return;
      }
      this.FillUrls();
    },
    move_input() {
      document.body.scrollTop = document.getElementById("search_form").offsetTop - 2;
    },
    search() {
      if (this.searchInput != "") {
        window.open("https://www.baidu.com/s?ie=utf-8&f=8&rsv_bp=1&tn=baidu&wd=" + encodeURIComponent(this.searchInput));
        this.searchInput = "";
        return false;
      }
    },
    FillUrls() {
      window.ShowDiv = this.ShowDiv
      var strdomin = $.trim(this.searchInput);
      var qsData = { 'wd': strdomin, 'p': '3', 'cb': 'ShowDiv', 't': '1324113456725' };
      $.ajax({
        async: false,
        url: "http://suggestion.baidu.com/su",
        type: "GET",
        dataType: 'jsonp',
        jsonp: 'jsoncallback',
        data: qsData,
        timeout: 5000,
        success: function (json) {
        },
        error: function (xhr) {
          // alert(xhr);
        }
      });
    },
    ShowDiv(strurls) {
      var urls = strurls["s"];
      if(urls.length){
        this.isSuggestVisible = true;
      }
      this.urls=urls

    }
  }
};
</script>

<style scoped lang="less">
@import "./style/yhbg";
</style>
